import React, { Component } from 'react';
import Header from './Header';
import Order from './Order';
import Inventory from './Inventory';
import Fish from './Fish';
import samplesFishes from '../sample-fishes';
import base from '../base';

export default class App extends Component {
  constructor() {
    super();

    this.state = {
      fishes: {},
      order: {}
    }

    this.addFish = this.addFish.bind(this);
    this.loadSamples = this.loadSamples.bind(this);
    this.addToOrder = this.addToOrder.bind(this);
    this.removeFromOrder = this.removeFromOrder.bind(this);
    this.updateFish = this.updateFish.bind(this);
    this.removeFish = this.removeFish.bind(this);
  }
  
  componentWillMount() {
    this.ref = base.syncState(`/${this.props.match.params.storeId}/fishes`, {
      context: this,
      state: 'fishes'
    });

    const order = localStorage.getItem(`order-${this.props.match.params.storeId}`);
    if (order) {
      this.setState({
        order: JSON.parse(order)
      });
    }
  }
  componentWillUnmount() {
    base.removeBinding(this.ref);
  }
  componentWillUpdate(nextProps, nextState) {
    console.log('something changed', {nextProps, nextState});

    localStorage.setItem(`order-${this.props.match.params.storeId}`, JSON.stringify(nextState.order));
  }
  // shouldComponentUpdate(nextProps, nextState) {
  //   return nextState.order;
  // }
  addFish(fish) {
    const updatedFishState = {...this.state.fishes};

    updatedFishState[`fish-${Date.now()}`] = fish;

    this.setState({
      fishes: updatedFishState
    });
  }
  loadSamples() {
    this.setState({
      fishes: samplesFishes
    });
  }
  addToOrder(key) {
    const order = {...this.state.order};

    order[key] = order[key] + 1 || 1;

    this.setState({
      order
    });
  }
  removeFromOrder(key) {
    const order = {...this.state.order};

    delete order[key];

    this.setState({
      order
    });
  }
  updateFish(key, updatedFish) {
    const fishes = {...this.state.fishes};
    fishes[key] = updatedFish;
    this.setState({ fishes });
  }
  removeFish(key) {
    const fishes = {...this.state.fishes};
    fishes[key] = null;
    this.setState({ fishes });
  }
  render () {
    return (
      <div className='catch-of-the-day'>
        <div className='menu'>
          <Header tagLine='Fresh Seafood Market' />
          <ul className='list-of-fishes'>
            {
              Object
                .keys(this.state.fishes)
                .map((key) => <Fish 
                  key={key} 
                  index={key} 
                  details={this.state.fishes[key]} 
                  addToOrder={this.addToOrder} />)
            }
          </ul>
        </div>
        <Order removeFromOrder={this.removeFromOrder} fishes={this.state.fishes} order={this.state.order} props={this.props} />
        <Inventory storeId={this.props.match.params.storeId} removeFish={this.removeFish} addFish={this.addFish} loadSamples={this.loadSamples} fishes={this.state.fishes} updateFish={this.updateFish} />
      </div>
    );
  }
}


